@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Home page */
.home-page section:nth-child(even) {
  background-color: #F5F5F5;
}

/* formats the contents of each section */
.summary, .try-it {
  /* height: 370px; */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

}

.summary *, .try-it * {
  width: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.why {
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.vertical-padding {
  padding-top: 60px;
  padding-bottom: 60px;
}

.horizontal-content {
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}

.horizontal-content p {
  text-align: center;
  width: 200px;
}

.why p {
  width: 60%;
}

p {
  text-align: left;
}

section {
  padding-left: 100px;
  padding-right: 100px;
}

.summary {
  height: 650px;
}

/* all sections */
img {
  width: 100%;
}
.contact-card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  min-width: 360px; 
}

.contact-card:nth-child(even) {
  margin-left: 50px;
}

.img-container {
  width: 115px;
  margin-right: 40px;
}

.contact-details {
  text-align: left;
}

.contact-details * {
  margin: 0px;
  padding: 0px;
}

.contact-details h2 {
  margin-bottom: 10px;
}

.contact-details a {
  color: '#4A69D9';
}
.about-page {
  padding-bottom: 50px;
}

.overview {
  height: 380px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

}

.overview * {
  width: 60%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 50px;
}
/* Navbar */
.logo {
  margin: 3px;
}

nav {
  background-color: white;
  border-bottom: solid #4A69D9 3px;
  /* margin: 5px; */
  padding: 30px;
  padding-top: 15px;
  padding-bottom: 20px;
  
  display: -webkit-flex;
  
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

nav a:hover, nav span {
  color: black;
  text-decoration: none; 
  cursor: pointer;
}

nav a, #nav-links {
  color: black;
  font-size: 18px;
  padding-left: 5px;
  padding-right: 5px;
  text-decoration: none; 
}

.navbar-brand {
  font-weight: bold;
  font-size: 44px;
  text-decoration: none;
}

#nav-links {
  font-size: 20px;
  padding-top: 10px;
}


/* General page style */

/* Default */
.App {
  text-align: center;
  font-family: 'Mulish', 'sans-serif';
  font-weight: 400;
}

h1 {
  font-size: 45px;
  font-weight: 800;
}

h2 {
  font-size: 32px;
  font-weight: 800;
}

h3 {
  font-size: 24px;
  font-weight: 700;
}

p {
  font-size: 18px;
  font-weight: 400;
}
